h1 {
  color: Black;
  text-align: center;
  font-family: sans-serif;
}
p {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

#air {
  margin-top: 20px;
  text-align: center;
  font-family: sans-serif;
  color: #364e96; /*文字色*/
  padding: 0em 0; /*上下の余白*/
  border-top: solid 5px #ed6c02; /*上線*/
  border-bottom: solid 5px #ed6c02;
  border-left: solid 5px #ed6c02;
  border-right: solid 5px #ed6c02;
}

#air2 {
  margin-top: 20px;
  text-align: center;
  font-family: sans-serif;
  color: #364e96; /*文字色*/
  padding: 0em 0; /*上下の余白*/
  border-top: solid 3.5px #ed6c02; /*上線*/
  border-bottom: solid 3.5px #ed6c02; /*下線*/
}

.flex {
  display: flex; /* フレックスボックスにする */
  flex-wrap: wrap; /* 折り返し指定 */
  justify-content: space-around;
}

.item00 {
  border-top: solid 12px #ed6c02; /*上線*/
  border-bottom: solid 12px #ed6c02;
  border-left: solid 12px #ed6c02;
  border-right: solid 12px #ed6c02;
}
.item0 {
  width: 50%;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

.item {
  width: 23.3%;
  text-align: center;
}
.item2 {
  width: 100%;
  text-align: center;
}
.item3 {
  width: 40%;
  text-align: center;
}
.item4 {
  width: 60%;
  text-align: center;
}
.item5 {
  width: 80%;
  text-align: center;
}
header {
  display: flex;
  justify-content: center;
}

.menu {
  margin-right: 30px;
}
.font2 {
  font-size: larger;
}
.font3 {
  font-size: 60px;
}
.font4 {
  font-size: 30px;
}

.min {
  min-width: 600px;
}
